// --------------------------------------------------
// fonts
// --------------------------------------------------

// font-face 선언 만들기
@mixin font-face($family, $filename, $weight: 400) {
  //noinspection CssUnknownTarget
  @font-face {
    font-family: '#{$family}';
    font-style: normal;
    font-weight: $weight;
    font-display: swap;
    src: local('#{$filename}');
    src: url('/static/fonts/#{$filename}.eot');
    src: url('/static/fonts/#{$filename}.woff2') format('woff2'), url('/static/fonts/#{$filename}.woff') format('woff');
  }
}

// 리스트에 있는 항목을 모두 선언
@each $filename, $props in $font-list {
  @include font-face(nth($props, 1), $filename, nth($props, 2));
}
