// -----------------------------------------------------------------------------
// mixins
// -----------------------------------------------------------------------------

// a11y
// -----------------------------------------------------------------------------
@mixin a11y {
  position: absolute !important;
  overflow: hidden;
  height: 1px;
  width: 1px;
  // clip: rect(1px 1px 1px 1px); // for IE6, IE7
  clip: rect(1px, 1px, 1px, 1px);
}

// -----------------------------------------------------------------------------
@mixin clearfix {
  zoom: 1;

  &:before {
    content: '';
    display: block;
  }

  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin l-column($column: null) {
  $gutter: 10;
  $grid: 74 + $gutter;
  $mobile: 360;
  $desktop: $w-default;
  $container: $w-large;

  $max-column: 12;

  position: relative;

  @if $column {
    $col: ($max-column - $column)/2;
    $pad: $gutter * $col;
    $layout: ($grid + $gutter) * $col;
    $margin-gutter: ($container - $desktop)/2;

    margin-left: percentage($p-small/$w-mobile);
    margin-right: percentage($p-small/$w-mobile);

    @include mq(large) {
      @if $col != 0 {
        padding-left: calc(#{percentage($layout)} / #{$container});
        padding-right: calc(#{percentage($layout)} / #{$container});
      }
      margin-left: percentage($margin-gutter/$container);
      margin-right: percentage($margin-gutter/$container);
    }

    @include mq(x-large) {
      @if $column == 12 {
        margin-left: percentage($margin-gutter/$container);
        margin-right: percentage($margin-gutter/$container);
      }
    }
  } @else {
    @include mq(large) {
      max-width: 1280px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

@mixin l-pad {
  $desktop: $w-default;
  $container: $w-large;

  $margin-gutter: ($container - $desktop)/2;

  padding-left: percentage($p-small/$w-mobile);
  padding-right: percentage($p-small/$w-mobile);

  @include mq(large) {
    padding-left: percentage($margin-gutter/$w-large);
    padding-right: percentage($margin-gutter/$w-large);
  }
}

@mixin l-layout {
  @include mq(large) {
    max-width: $w-default + px;
    margin-left: auto;
    margin-right: auto;
    padding-left: percentage($number: 60 / $w-default);
    padding-right: percentage($number: 60 / $w-default);
  }
}
// -----------------------------------------------------------------------------
@mixin break-word {
  word-break: keep-all;
  word-wrap: break-word;
}

@mixin bg-img($foldername, $filename) {
  background-image: url(#{$foldername}/#{$filename});
}

@mixin full-pos {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@mixin ellipsis($lines) {
  overflow: hidden;
  display: inline-block;
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  word-wrap: normal;
}

@mixin opacity($value) {
  $valuePercent: $value * 100;

  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=#{#valuePercent})';
  filter: alpha(opacity=$valuePercent);
  opacity: $value;
}

@mixin v-mid($child: '&-cell') {
  white-space: nowrap;

  #{$child} {
    display: inline-block;
    vertical-align: middle;
    white-space: normal;
    @content;
  }

  &:after {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
}

@mixin triangle($size, $color, $direction) {
  $width: nth($size, 1);
  $height: nth($size, length($size));
  $foreground-color: nth($color, 1);
  $background-color: transparent;
  height: 0;
  width: 0;

  @if ($direction == up) or
    ($direction == down) or
    ($direction == right) or
    ($direction == left)
  {
    $width: $width / 2;
    $height: if(length($size) > 1, $height, $height/2);

    @if $direction == up {
      border-bottom: $height solid $foreground-color;
      border-left: $width solid $background-color;
      border-right: $width solid $background-color;
    } @else if $direction == right {
      border-bottom: $width solid $background-color;
      border-left: $height solid $foreground-color;
      border-top: $width solid $background-color;
    } @else if $direction == down {
      border-left: $width solid $background-color;
      border-right: $width solid $background-color;
      border-top: $height solid $foreground-color;
    } @else if $direction == left {
      border-bottom: $width solid $background-color;
      border-right: $height solid $foreground-color;
      border-top: $width solid $background-color;
    }
  }
}

@mixin gradient($direction, $color-stops...) {
  @if is-direction($direction) == false {
    $color-stops: $direction, $color-stops;
    $direction: 180deg;
  }

  background: nth(nth($color-stops, 1), 1);
  background: -webkit-linear-gradient($direction, $color-stops);
  background: -moz-linear-gradient($direction, $color-stops);
  background: linear-gradient($direction, $color-stops);

  $start-color: nth($color-stops, 1);
  $end-color: nth($color-stops, 2);

  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#{$start-color}', endColorstr='#{$end-color}',GradientType=1 ); // IE6-9 fallback on horizontal gradient
}

@mixin radial-gradient($options, $start, $start-pos, $end, $end-pos) {
  @if ($options) == false {
    $color-stops: $start, $start-pos, $end, $end-pos;
    $options: circle farthest-corner at 50% 50%;
  }

  background: -moz-radial-gradient($options, $start $start-pos, $end $end-pos);
  background: -webkit-radial-gradient(
    $options,
    $start $start-pos,
    $end $end-pos
  );
  background: -o-radial-gradient($options, $start $start-pos, $end $end-pos);
  background: -ms-radial-gradient($options, $start $start-pos, $end $end-pos);
  background: radial-gradient($options, $start $start-pos, $end $end-pos);
}

@mixin container() {
  box-sizing: border-box;
  position: relative;
  max-width: $w-default + px;
  margin-left: auto;
  margin-right: auto;
}
