// --------------------------------------------------
// variables
// --------------------------------------------------

// breakpoints
$mq-breakpoints: (
  mobile: 321px,
  desktop: 980px,
  // Tweakpoints
    x-small: 400px,
  small: 560px,
  medium: 720px,
  large: 960px,
  x-large: 1280px,
  xx-large: 1600px
);

// layout
$w-mobile: 375;
$w-xsmall: 732;
$w-small: 920;
$w-default: 1080;
$w-large: 1280;
$w-max: 1920;
$w-grid: 74;

$p-small: 20;
$p-medium: 43;
$p-large: 86;

$gutter-small: 10;
$gutter-large: 20;

// fonts
$font-list: (
  // [filename]: ([font-family]: [weight])
    SDpwingGothicNeoaUniTTF-aTh: (SDpwingUni, 100),
  SDpwingGothicNeoaUniTTF-bUl: (SDpwingUni, 200),
  SDpwingGothicNeoaUniTTF-dRg: (SDpwingUni, 400),
  SDpwingGothicNeoaUniTTF-eMd: (SDpwingUni, 500),
  SDpwingGothicNeoaUniTTF-gBd: (SDpwingUni, 700),
  SDpwingGothicNeoRoundTTF-dRg: (SDpwingRound, 100),
  SDpwingGothicNeoRoundTTF-eMd: (SDpwingRound, 200),
  SDpwingGothicNeoRoundTTF-gBd: (SDpwingRound, 400),
  SDpwingGothicNeoRoundTTF-hEb: (SDpwingRound, 500),
  SDpwingGothicNeoRoundTTF-iHv: (SDpwingRound, 700)
);

$font-default: 'SDpwingUni', 'Malgun Gothic', dotum, sans-serif;
$font-em: 'SDpwingRound', dotum, sans-serif;

$font-icon: 'icon';
$icons: ();

