@charset "UTF-8";
// -----------------------------------------------------------------------------
// base styles
// -----------------------------------------------------------------------------

html {
  font-size: 62.5%;
}
html,
body {
  min-height: 100%;
}
body {
  font-size: 1em;
  line-height: 1.5;
  -webkit-overflow-scrolling: touch;
}
a {
  color: $black;
  text-decoration: none;
}
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}
ul,
ol,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
}
ul,
ol,
li {
  list-style: none;
}
em,
address {
  font-style: normal;
}
figure,
form,
p {
  margin: 0;
}
fieldset {
  margin: 0;
  padding: 0;
  border: 0 none;
  vertical-align: top;
}
label {
  cursor: pointer;
}
button,
input,
select,
textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}
button {
  -webkit-appearance: none;
  box-sizing: border-box;
  padding: 0;
  border: 0 none;
  background: transparent;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}
input[type='text'],
input[type='email'],
input[type='number'],
input[type='password'] {
  box-sizing: border-box;
  border: 0 none;
  font-size: 1.3rem;
}
input[type='checkbox'],
input[type='radio'] {
  vertical-align: middle;
}
input[type='search'] {
  -webkit-appearance: none;
  border-radius: 0;
}
textarea {
  vertical-align: top;
  resize: vertical;
}
input:invalid,
textarea:invalid {
  background-color: $ash;
}
table {
  width: 100%;
  empty-cells: show;
}
td {
  vertical-align: middle;
}
select {
  vertical-align: middle;
  border-radius: 0;
}
::selection {
  background: $brand;
  color: $white;
  text-shadow: none;
}
::-webkit-input-placeholder {
  color: $silver-two;
}
::-ms-input-placeholder {
  color: $silver-two;
}
:-moz-placeholder {
  color: $silver-two;
}
em,
mark {
  background-color: transparent;
  color: $pinkish-red;
}

// base guide
// --------------------------------------------------
body,
input,
textarea,
select,
button {
  font-family: $font-default;
  color: $color-body;
}
html {
  background-color: $ash;
}
body {
  min-width: 320px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
  @include break-word;
}
p {
  // word-spacing: 0.02em;
  // letter-spacing: -0.05em;
  @include break-word;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: top;

  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
}
svg {
  display: inline-block;
}
iframe {
  border: 0;
}
