// -----------------------------------------------------------------------------
// Helper classes
// -----------------------------------------------------------------------------

// hide element
[hidden],
.hidden {
    display: none;
}

// accessibility
.a11y {
	@include a11y;
}

// flexible objects
.flexible-obj {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;

    iframe,
    object,
    video,
    embed,
    > img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}